import { filter } from 'lodash';
import { Icon } from '@iconify/react';
import { sentenceCase } from 'change-case';
import { useState, useEffect } from 'react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Link as RouterLink } from 'react-router-dom';
import { addMonths, startOfDay } from 'date-fns';
// material
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  Stack,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination
} from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getFacturations, getCompagnies } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { FactuListHead, FactuListToolbar, FactuMoreMenu } from '../../components/_dashboard/factu/list';
import { fDate, fDateTime } from '../../utils/formatTime';
import useAuth from '../../hooks/useAuth';
// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'name', label: 'Compagnie', alignRight: false },
  { id: 'price', label: 'Prix', alignRight: false },
  { id: 'nb_agents', label: 'nombres agents', alignRight: false },
  { id: 'total', label: 'Total', alignRight: false },
  { id: '' }
];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array?.map((el, index) => [el, index]);
  stabilizedThis?.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(
      array,
      (_tracabiliteAgent) => _tracabiliteAgent?.agent_first_name.toLowerCase().indexOf(query.toLowerCase()) !== -1
    );
  }
  return stabilizedThis?.map((el) => el[0]);
}

export default function Facturation() {
  const { user } = useAuth();
  const { themeStretch } = useSettings();
  const theme = useTheme();
  const dispatch = useDispatch();
  const { facturations, compagnies } = useSelector((state) => state.user);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('first_name');
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(25);
  const today = new Date();
  const oneMonthAgo = addMonths(today, -1);
  const [value, setValue] = useState(startOfDay(oneMonthAgo));
  const [value1, setValue1] = useState(startOfDay(today));
  const [value2, setValue2] = useState(0);
  const [loading, setLoading] = useState(false);
  const onChangeDate1 = (datavalue) => {
    setValue(datavalue);
  };
  const onChangeDate2 = (datavalue) => {
    setValue1(datavalue);
  };
  const handleGo = async () => {
    setLoading(true);
    await dispatch(getFacturations(fDate(value), fDate(value1), value2));
    setLoading(false);
  };

  useEffect(() => {
    // handleGo();
    dispatch(getCompagnies());
  }, [dispatch]);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = facturations?.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - facturations.length) : 0;

  const filteredFacturations = applySortFilter(facturations, getComparator(order, orderBy), filterName);

  const isFacturationNotFound = filteredFacturations?.length === 0;

  return (
    <Page title="Facturation: List | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Liste Facturation"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Facturation', href: PATH_DASHBOARD.company.companyFactu },
            { name: 'Listes' }
          ]}
        />

        <Card>
          <FactuListToolbar
            value={value}
            value1={value1}
            value2={value2}
            setValue2={setValue2}
            compagnies={compagnies}
            onChangeDate1={onChangeDate1}
            onChangeDate2={onChangeDate2}
            handleGo={handleGo}
            loading={loading}
            setLoading={setLoading}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table>
                <FactuListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={facturations?.length}
                  numSelected={selected?.length}
                  onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                />
                <TableBody>
                  {(rowsPerPage > 0
                    ? filteredFacturations?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    : filteredFacturations
                  )?.map((ite) => {
                    const isItemSelected = selected.indexOf(ite?.id) !== -1;

                    return (
                      <TableRow
                        hover
                        key={ite?.id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell component="th" scope="row" padding="none">
                          <Stack direction="row" alignItems="center" spacing={2}>
                            <Avatar alt={ite?.companyName} src={ite?.company_photo} />
                            <Typography variant="subtitle2" noWrap>
                              {ite?.companyName}
                            </Typography>
                          </Stack>
                        </TableCell>
                        <TableCell align="left">{ite?.price}</TableCell>
                        <TableCell align="left">{ite?.agents}</TableCell>
                        <TableCell align="left">{ite?.total}</TableCell>
                        <TableCell align="right">
                          <FactuMoreMenu factuId={ite?.id} />
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
                {isFacturationNotFound && (
                  <TableBody>
                    <TableRow>
                      <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                  </TableBody>
                )}
              </Table>
            </TableContainer>
          </Scrollbar>

          <TablePagination
            labelRowsPerPage="Lignes par page"
            rowsPerPageOptions={[25, 50, 75, 100, { label: 'Tout', value: -1 }]}
            component="div"
            count={facturations?.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Card>
      </Container>
    </Page>
  );
}
